// Styling for the footer
footer {
    background: $gray-900;
    padding: 35px;
    text-align: center;
    span.copyright {
      color: white; 
      font-size: 90%;
      line-height: 40px;
      text-transform: none;
      @include heading-font;
    }
    ul.quicklinks {
      font-size: 90%;
      line-height: 40px;
      margin-bottom: 0;
      text-transform: none;
      @include heading-font;
    }
  }
  
  ul.social-buttons {
    margin-bottom: 0;
    li {
      a {
        font-size: 20px;
        line-height: 50px;
        display: block;
        width: 50px;
        height: 50px;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        transition: all 0.3s;
        color: $primary;
        border-radius: 100%;
        outline: none;
        background-color: $gray-300;
        &:active,
        &:focus,
        &:hover {
          color: $white;
          background-color: $primary;
        }
      }
    }
  }
  